@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@500;600&display=swap);
:root {
  /*### Colors */
  /*### Primary*/
  --Moderate-violet: hsl(263, 55%, 52%);
  --Very-dark-grayish-blue: hsl(217, 19%, 35%);
  --Very-dark-blackish-blue: hsl(219, 29%, 14%);
  --White: hsl(0, 0%, 100%);

  /*### Neutral*/
  --Light-gray: hsl(0, 0%, 81%);
  --Light-grayish-blue: hsl(210, 46%, 95%);
}
body {
  font-family: "Barlow Semi Condensed", sans-serif;
  background-color: hsl(210, 46%, 95%);
  background-color: var(--Light-grayish-blue);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.appCSS_allCardsContainer__HxiAi {
  margin-top: 70px;
  margin-left: 24px;
  margin-right: 24px;
}

@media only screen and (min-width: 1000px) {
  body {
    height: 100vh;
    /*flex*/
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .appCSS_allCardsContainer__HxiAi {
    margin: 0;
    max-width: 1114px;
    max-height: 577px;
    display: grid;
    grid-gap: 30px;
    grid-template-columns: repeat(4, 1fr);
  }
}

figure {
  margin: 0;
}
.cardCSS_card__nwc1q {
  border-radius: 11px;
  padding-top: 24px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 24px;
  margin-bottom: 24px;
}
#cardCSS_card1__187wD {
  background-color: var(--Moderate-violet);
  color: var(--White);
  position: relative;
}
#cardCSS_card1__187wD figure {
  background-image: url(../../static/media/bg-pattern-quotation.6b460e30.svg);
  width: 104px;
  height: 102px;
  position: absolute;
  top: 0;
  right: 24px;
  z-index: 1;
}
#cardCSS_card2__3S_5_ {
  background-color: var(--Very-dark-grayish-blue);
  color: var(--White);
}
#cardCSS_card3__2GBhW {
  background-color: var(--White);
  color: var(--Very-dark-grayish-blue);
  box-shadow: 35px 35px 40px -35px var(--Light-gray);
}
#cardCSS_card4__FpdQ1 {
  background-color: var(--Very-dark-blackish-blue);
  color: var(--White);
}
#cardCSS_card5__2_5Wf {
  background-color: var(--White);
  color: var(--Very-dark-grayish-blue);
  box-shadow: 35px 35px 40px -35px var(--Light-gray);
}
/*---DESKTOP---*/
@media only screen and (min-width: 1000px) {
  .cardCSS_card__nwc1q {
    margin-bottom: 0;
  }
  #cardCSS_card1__187wD {
    grid-column: span 2;
  }
  #cardCSS_card4__FpdQ1 {
    grid-column: span 2;
  }
  #cardCSS_card5__2_5Wf {
    grid-column-start: 4;
    grid-row-start: 1;
    grid-row-end: 3;
  }
}

